import { useEffect } from "react";
import { Route, Switch, useLocation } from "react-router-dom";
import "./App.css";
import { Footer } from "./components/Footer/Footer";
import { ModalContainer } from "./components/ModalContainer/ModalContainer";
import { Sidebar } from "./components/Sidebar/Sidebar";
import { Malaysia, Singapore } from "./pages";
import { routes } from "./constants/routes";
import { goFullScreen, exitFullscreen } from "./utils/fullscreenHelper";
import { useWindowSize } from "./utils/newScreenHeightHelper";
// import { height } from "./constants/styling";
// import useWindowDimensions from "./utils/screenHeightHelper";

// const getDomain =
//   process.env.NODE_ENV === "development"
//     ? "http://localhost:3000/"
//     : "https://panasonic.com/";

const getVistaUrl = (country) => {
  const urlSwitch = {
    malaysia: "/space-solution/malaysia/malaysia_r16/index.htm",
  };

  return urlSwitch[country];
};


function App() {

  // const { height, width } = useWindowDimensions();
  const [windowWidth ,windowHeight] = useWindowSize();

  useEffect(() => {
    function callback(e) {
      if (e.data.fullscreen) {
        goFullScreen();
      } else if (e.data.exit) {
        exitFullscreen();
      }
    }
    window.addEventListener("message", callback);
  });

  const location = useLocation();
  return (
    <div className="App" style={{ minHeight: windowHeight, maxWidth: windowWidth }}>
      <Sidebar />
      <ModalContainer />
      <Switch>
        <Route exact path={routes.my} component={() => <Malaysia getVistaUrl={getVistaUrl} />}></Route>
      </Switch>
      <Switch>
        <Route exact path={routes.sg} component={() => <Singapore getVistaUrl={getVistaUrl} />}></Route>
      </Switch>
      {location.pathname === "/" ? "" : <Footer />}
    </div>
  );
}

export default App;
