import React from "react";
// import useWindowDimensions from "../utils/screenHeightHelper"
// import { height } from "../constants/styling";
import { useWindowSize } from "../utils/newScreenHeightHelper";

const MalaysiaComponent = ({ getVistaUrl }) => {
  // const { height, width } = useWindowDimensions();
  const [windowWidth, windowHeight] = useWindowSize();

  return (
    <>
      <iframe
        style={{ minHeight: windowHeight, width: "100%" }}
        allowFullScreen={false}
        title="Malaysia"
        src={getVistaUrl("malaysia")}
      ></iframe>
    </>
  );
};

const Malaysia = React.memo(MalaysiaComponent);

export { Malaysia };
