import React, { useMemo } from "react";
import { Modal } from "react-bootstrap";
import { ImageCarousel } from "../ImageCarousel/ImageCarousel";
import styles from "./ProductModal.module.scss";

const ProductModal = ({
  openVideoModal,
  PRODUCTS,
  IMAGES,
  findProduct,
  getVariations,
  getKeySpecs,
  getIndications,
  handleClose,
  openVariant,
  setVariant,
  variant,
  setOpenVariant,
  modal,
  openARModal,
}) => {
  return useMemo(() => {
    const toggleVariant = () => {
      setVariant(null);
      setOpenVariant(!openVariant);
    };

    if (!modal) return null;
    const {
      Model,
      Site,
      Link,
      Indication,
      ProductTitle,
      HasAR,
      ImagePath,
      VideoLink,
      APText,
      APIcons
      // Variations = [],
    } = modal;
    const getProductImages = (model) =>
      IMAGES.keys()
        .filter((file) => file.startsWith(`./${ImagePath}`))
        .map((filePath) => `${process.env.PUBLIC_URL}/product-images${filePath.substring(1)}`);
    return (
      <Modal backdrop={false} show={Boolean(modal)} onHide={handleClose}>
        <div className={styles.container}>
          <div className={styles.carouselColumn}>
            <ImageCarousel images={getProductImages(variant ? variant.Model : Model)} ProductTitle={ProductTitle} />
          </div>
          <div className={styles.productDetails}>
            <div className={styles.excerpt}>
              <div className={styles.excerptLeft}>
                <img src={process.env.PUBLIC_URL + `/${APIcons}`} alt="Product Type Icon" className={styles.appleLogo} />
                <p>{APText}</p>
              </div>
              <button onClick={handleClose} type="button" className="close" data-dismiss="modal">
                ×
              </button>
            </div>
            <div>
              <div className={styles.productTitle}>
                <p onClick={toggleVariant}>
                  {ProductTitle} &nbsp; {getVariations(Model, Site).length > 0 && <i className="fas fa-caret-down"></i>}
                </p>
                {openVariant &&
                  getVariations(Model, Site).map((variantProduct) => (
                    <p onClick={() => setVariant(variantProduct)}>{variantProduct.ProductTitle}</p>
                  ))}
                <div>
                  {variant ? (
                    <ul className={styles.productDescription}>
                      {variant.KeySpecs.map((spec, index) => {
                        return spec !== "" ? <li key={index}>{spec}</li> : "";
                      })}
                    </ul>
                  ) : (
                    <ul className={styles.productDescription}>
                      {getKeySpecs(findProduct(PRODUCTS, Model, Site))
                        .slice(0, 4)
                        .map((spec, index) => {
                          return spec !== "" ? <li key={index}>{spec}</li> : "";
                        })}
                    </ul>
                  )}
                </div>
              </div>
            </div>
            <div className={styles.indicationAndAR}>
              <div>
                {Indication
                  ? getIndications(Indication).map((ind, index) => {
                      return (
                        <li key={index} className={styles.indication}>
                          {ind}
                        </li>
                      );
                    })
                  : null}
              </div>
              <div>
                {HasAR ? (
                  <>
                    <p className={styles.arText}>Scan/Click for AR</p>
                    <img src={process.env.PUBLIC_URL + "/images/qrar.png"} className={styles.qrCode} alt="QR Code" onClick={openARModal} />
                  </>
                ) : null}
              </div>
            </div>
            <div className={styles.btnContainer}>
              {VideoLink && (
                <button className={styles.btn} onClick={VideoLink && openVideoModal}>
                  Watch Video
                </button>
              )}
              <button className={styles.btn}>
                <a href={Link} rel="noreferrer" target="_blank">
                  See More Details
                </a>
              </button>
            </div>
          </div>
        </div>
      </Modal>
    );
  }, [
    modal,
    handleClose,
    variant,
    getVariations,
    openVariant,
    getKeySpecs,
    findProduct,
    PRODUCTS,
    getIndications,
    openVideoModal,
    setVariant,
    setOpenVariant,
    IMAGES,
    openARModal,
  ]);
};

export { ProductModal };
