import React from "react";
import { useWindowSize } from "../utils/newScreenHeightHelper";

const Singapore = ({ getVistaUrl }) => {
  const [windowWidth, windowHeight] = useWindowSize();
  return (
    <div style={{ height: windowHeight, backgroundColor: "pink", width: windowWidth, overflow:"hidden" }}>
      <iframe
        style={{ height: "100%", width: "100vw", overflow: "hidden" }}
        allowFullScreen={false}
        title="Malaysia"
        src={getVistaUrl("malaysia")}
      ></iframe>
      {/* <iframe title="Malaysia" src={getVistaUrl('malaysia')}></iframe> */}
    </div>
  );
};

export { Singapore };
